<template>
  <div id="show-words"> 
        <div id="custom-search-input">
            <div class="input-group col-md-12">
                <input type="text"  v-model="search" class="form-control input-lg search-bar" placeholder="" />
                <span class="input-group-btn">
                    <button class="btn btn-info btn-lg" type="button">
                        <i class="glyphicon glyphicon-search"></i>
                    </button>
                </span>
            </div>
        </div>

<br>
 
  <div v-for="(word, index) in filteredWords.slice(0, 20)" class="single-word">

   <article class="results">
     <strong>{{word.f}}</strong> : 

     <strong> {{word.p}} </strong>
   </article> 
   <br>  

 

  </div>

  
  </div>
</template>

<script>



export default {
 
  name: 'Search',
  data () {
    return {
      words: [],
      search: ""
      
    }
  },

  created() {
    this.words = require('../assets/parsig.json');

   // this.words = JSON.parse('../assets/parsig.json');
     // this.$http.get('http://golvaje.top/api/jokes/1/0').then(function(data){
     //   this.words = data.body.slice(0,20);
     // });
 
    
  
   } ,

  computed: {
    filteredWords: function() {
      return this.words.filter((word) => {
        return word.f.match(this.search)
      });
    }
  }
}
</script>

